<template>
<div class="report-main">
    <div class="title">被举报人信息</div>
    <div class="form-main">
        <div class="input-box">
            <div class="label bg1">姓名</div>
            <input type="text" placeholder="请输入被举报人的姓名" v-model="name">
        </div>
        <div class="input-box">
            <div class="label bg2">单位名称</div>
            <input type="text" placeholder="请输入被举报人的单位名称" v-model="company">
        </div>
        <div class="input-box">
            <div class="label bg3">职务名称</div>
            <input type="text" placeholder="请输入被举报人的职务名称" v-model="position">
        </div>
        <div class="input-box">
            <div class="label bg4">举报内容</div>
            <textarea placeholder="请描述您要举报的内容详情" v-model="content"></textarea>
        </div>
    </div>
    <div class="tip"><span>*</span>举报需实名，本平台承诺对您的信息进行保密。</div>
    <div class="form-main">
        <div class="input-box">
            <div class="label bg1">您的姓名</div>
            <input type="text" placeholder="请输入您的姓名" v-model="reportName">
        </div>
        <div class="input-box">
            <div class="label bg5">联系方式</div>
            <input type="text" placeholder="请输入您的手机号码" v-model="contact">
        </div>
    </div>
    <el-button type="primary" @click="submit">提      交</el-button>
</div>
</template>

<script>

export default {
  name: "Report",
  data() {
    return {
        name: '',
        company: '',
        position: '',
        content: '',
        reportName: '',
        contact: ''
    }
  },

  methods: {
      submit() {
          if (this.name.trim() === '' || this.company.trim() === '' || this.position.trim() === '' || this.content.trim() === '' || this.reportName.trim() === '' || this.contact.trim() === '') {
              alert('重要信息不能为空')
              return
          } 
          
          if(!(/^1[3456789]\d{9}$/.test(this.contact.trim()))){
              alert('手机号格式不正确')
              return
          }

          this.$axios({
                method: 'post',
                url: '/api/report/add',
                data: {
                    name: this.name,
                    company: this.company,
                    position: this.position,
                    content: this.content,
                    reportName: this.reportName,
                    contact: this.contact
                }
            }).then(res => {
                if (res.data.code === 1000) {
                    alert('举报信息提交成功')
                    this.reset()
                } else {
                    alert(res.data.message)
                }
            }).catch(err => {
                console.log(err)
                alert('系统异常，请稍后重试')
            })
      },
      reset() {
            this.name = ''
            this.company = ''
            this.position = ''
            this.content = ''
            this.reportName = ''
            this.contact = ''
      }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.report-main {
    ::-webkit-input-placeholder { /* WebKit browsers */
        color: #999;
        font-size: 16px;
    }
    .title {
        height: 50px;
        line-height: 50px;
        padding-left: 25px;
    }
    .form-main {
        background-color: #ffffff;
        border-radius: 8px;
        margin: 0 15px;
        .input-box {
            overflow: hidden;
            border-bottom: 1px solid #f5f5f5;
            .label {
                width: 110px;
                float: left;
                height: 50px;
                line-height: 50px;
                padding-left: 40px;
                text-align: justify;
                text-align-last: justify;
            }
            input {
                font:400 16px Avenir, Helvetica, Arial, sans-serif;
                width: calc(100% - 110px);
                float: left;
                height: 50px;
                border: none;
                outline: none;
                line-height: 50px;
                color: #999999;
                border-radius: 8px;
                padding:0 15px;
            }
            textarea {
                font:400 16px Avenir, Helvetica, Arial, sans-serif;
                width: calc(100% - 110px);
                float: left;
                height: 122px;
                border: none;
                outline: none;
                line-height: 50px;
                color: #999999;
                border-radius: 8px;
                padding:13px 15px;
                line-height: 24px;
            }
            .bg1 {
                background: url(../assets/icon2.jpg) no-repeat 10px 14px / 20px auto;
            }
            .bg2 {
                background: url(../assets/icon3.jpg) no-repeat 10px 14px / 20px auto;
            }
            .bg3 {
                background: url(../assets/icon4.jpg) no-repeat 10px 14px / 20px auto;
            }
            .bg4 {
                background: url(../assets/icon5.jpg) no-repeat 10px 14px / 20px auto;
            }
            .bg5 {
                background: url(../assets/icon6.jpg) no-repeat 10px 14px / 20px auto;
            }
        }
    }
    .tip {
        height: 50px;
        line-height: 50px;
        font-size: 14px;
        text-align: center;
        color: #666666;
        span {
            color: red;
        }
    }
    .el-button--primary {
        width: calc(100% - 30px);
        margin: 30px 15px;
        font-size: 18px;
    }
}
</style>